import React, {useState, useEffect, ChangeEvent} from 'react';
import axios from 'axios';
import Toast from '../notification/Toast';
import { AiFillCaretDown, AiFillCaretUp, AiFillCaretRight } from "react-icons/ai";
import CategoryDrop from '../dropdown/CategoryDrop';

interface ModalProps {
  closeModal: any
}


interface ICategory {
  account_ID: number;
  title: string;
  annotations: number;
}



const AddCategoryModal = ({closeModal}: ModalProps) => {
  const [userCategories, setUserCategories] = useState<ICategory[]>([])
  const [categoryTitle, setCategoryTitle] = useState("")
  const [categoryAnnotation, setCategoryAnnotation] = useState("")
  const [alertMessage, setAlertMessage] = useState("")
  

  useEffect(() => {
    myCategories()  
    return () => {}
  }, [])

  const myCategories  = async () => {
    const { data } = await axios.get("categories/all");
    await setUserCategories(data)
  };

  const handleCategoryTitle = (e: ChangeEvent<HTMLInputElement>) => {
    const categoryTitle = e.currentTarget.value;
    setCategoryTitle(categoryTitle);
  };


  const handleCategoryComment = (e: ChangeEvent<HTMLInputElement>) => {
    const categoryComent = e.currentTarget.value;
    setCategoryAnnotation(categoryComent);
  };
  
  
  const submitNewCategory = async () => {
    console.log(userCategories)
     let hasDuplicateCategory = await userCategories.filter((obj) => {
      return obj.title  === categoryTitle;
    });

    if (hasDuplicateCategory.length > 0){
      setAlertMessage("Category Already Exists")
    } else {
      await axios({
        method: 'post',
        url: 'categories/create',
        headers: {}, 
        data: {
          category_title: categoryTitle,
          category_comment: categoryAnnotation, 
        }
      }).then(res => {      
        setAlertMessage(res.headers.msg ? res.headers.msg : "")
    
      }).catch(ex => {
        setAlertMessage(ex.headers.msg ? ex.headers.msg : "")      
      });
    }

      await myCategories()
    
  }
  return (
    <>
    <div style={{ display: "flex" }}>           
      <div
        style={{
          position: "absolute",
          height: "100vh",
          width: "100%",
          backgroundColor: "rgba(0,0,0,0.8)",       
          zIndex: "999"
        }}
      >
        <div
          style={{
            position: "relative",
            height: "100vh",
            width: "100%",
            display: "flex",
            alignItems: "center",
            justifyContent: "center",
          }}
        >
          <div className="popUp-modal ">
            <button className='close-modal-btn' onClick={closeModal}>X</button>
            {/* <form action="categories/create" method="post" className='flex-column' > */}
            <div  className='flex-column'>
              <div>Create New Category</div>              
              <input                 
                name="category_title"               
                className="modal-input"
                placeholder='Category Name'
                onChange={handleCategoryTitle}
                value={categoryTitle}
                required>                  
              </input>
              <input
                name="category_comment"
                className="modal-input"
                placeholder='Category Comments'
                onChange={handleCategoryComment}
                value={categoryAnnotation}>                 
              </input>
              <button className='modal-submit' onClick={submitNewCategory}>Save Category</button>
            </div>
            {/* </form> */}
          </div>
        </div>
      </div>
  
    </div>
    <Toast toastMsg={alertMessage}/>
    </>
  );
};


interface HeaderProps {
  handleCategory: any  
}

const HeaderModal = ({handleCategory}: HeaderProps) => { 
  return (
    <>
      <div className="header-modal">
        <button className="header-modal-items" onClick={handleCategory}>Add Categories</button>
        {/* <button className="header-modal-items" onClick={handleTask}>Create Tasks</button> */}
        <form action="/authentications/signOut" method="post">
          <button className="header-modal-items" >Sign Out</button>
        </form>
      </div>
    </>
  )
}

const Header = () => {
  const [acctDrop, setAcctDrop] = useState(false)
  const [showAddCategory, setShowAddCategory] = useState(false)

  const handleAccountSettings = () => {
    setAcctDrop(!acctDrop)
  }

  const handleCategory = () => {
    setShowAddCategory(!showAddCategory)
  }
 

  const closeModal = () => {
    setShowAddCategory(false)     
  }
  return (
    <>          
      {showAddCategory && <AddCategoryModal closeModal={closeModal}/>} 
      
      <div className="header-container" >
        <div className='flex-column'>
          <button className='header-account-btn' onClick={handleAccountSettings} style={{marginRight: "1rem", marginTop: "0.2rem"}}>
            {!acctDrop ? <AiFillCaretDown /> : <AiFillCaretUp />}
          </button>
          <div style={{position: "relative", marginRight: "1rem"}}>
            {acctDrop && <HeaderModal handleCategory={handleCategory} />}
          </div>  
        </div>
      </div>  
           
    </>
  )
}

export default Header